*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    background-color: #23252C;
    font-family: 'Inter', sans-serif;
}
.container{
    width: 25%;
    margin: 0 auto;
    height: 100vh;
    background-color: #1A1B21;
    text-align: center;
    display: flex;
    flex-direction: column;
    overflow: hidden;
}
.info .info-photo{
    width: 100%;
    scale: 102%;
    height: 400px;
}
.info .info-text{
    display: flex;
    flex-direction: column;
    gap: 5px;
    margin: 15px 0;
}
.info .info-text .full-name{
    font-weight: 700;
    font-size: 40px;
    color: #FFFFFF;
}
.info .info-text .speciality{
    color: #F3BF99;
}
.info .info-text .website-url{
    color: #FFFFFF;
    font-weight: 300;
}
.info .info-btns{
    display: flex;
    justify-content: center;
    gap: 10px;
    padding: 0 30px;
}
.info .info-btns >*{
    border-radius: 6px;
    -webkit-border-radius: 6px;
    -moz-border-radius: 6px;
    -ms-border-radius: 6px;
    -o-border-radius: 6px;
    padding: 10px 20px;
    flex: 1;
    font-size: 14px;
    font-weight: normal;
    cursor: pointer;

}
.info .info-btns .btn-linkedin{
    color: white;
    background-color: #5093E2;
}

.main{
    padding: 40px 20px;
    text-align: left;
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.main .title{
    font-weight: 700;
    margin-bottom: 10px;
    color: white;
}
.main .paragraph{
    color: #DCDCDC;
    line-height: 1.5;
    font-size: 14px;
}
.footer{
    flex: 1;
    display: flex;
    justify-content: center;
    gap: 30px;
    align-items: center;
    background-color: #161619;
}
.footer >*{
    cursor: pointer;
}